<template>
  <div class="margin_left_right white_bg_color">
    <div>
      <hr class="hr_color">
      <el-row>
        <el-col :span="6">
          <div class="border"></div>
        </el-col>
        <el-col :span="18" class="text_align_left padding_left10">
            <div>
              <p class="stores_name">{{storesName}}</p>
              <p class="regular_font">
                <img class="icon_img" src="../../assets/medicalproduct/didian_tjcp_jgjs@2x.png" height="18"
                     width="18"/>
                <span>{{storesAddress}}</span>
              </p>
              <p class="regular_font">
                <img class="icon_img" src="../../assets/medicalproduct/dianhua_tjcp_jgjs@2x.png" height="18"
                     width="18"/>
                {{storePhone}}</p>
              <p class="regular_font">
                <img class="icon_img" src="../../assets/medicalproduct/shijian_tjcp_jgjs@2x.png" height="18"
                     width="18"/>
                {{businessHours}}
              </p>
              <p class="regular_font">

                <img class="icon_img" src="../../assets/medicalproduct/zhuyi_tjjg_jgxx@2x.png" height="18"
                     width="18" v-if="needingAttention !==null"/>
                {{needingAttention}}
              </p>
              <p class="regular_font">
                <img class="icon_img" src="../../assets/medicalproduct/jianjie_tjjg_jgxx@2x.png" height="18"
                     width="18"/>
                <span class="special_font">{{suitNumber}}</span><span class="special_font">{{suitPrice}}</span>
              </p>
            </div>
          <el-row class="margin_top15">
            <el-col :span="6">
              <el-button size="medium" class="select_suit" :class="{clickFont:active}" @click="selectSuit()">
              </el-button>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
    </div>
    <div class="margin_top15">
      <el-row :gutter="25">
        <el-col :span="19">
          <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
            <el-tab-pane label="" name="first">
              <div>
                <div class="store_info">
                  <el-row>
                    <el-col :span="6" class="padding_left10">
                      <div class="institution_img"></div>
                    </el-col>
                    <el-col :span="18" class="text_align_left">
                      <div>
                        <p class="stores_name">{{suitName}}<span><u class="special_font select_detail"></u></span>
                        </p>
                        <p class="margin_top0_bottom0"><span class="discount_price"></span><span
                          class="discount_price">{{salePrice}}</span><span class="origin_price"></span>&emsp;&emsp;&emsp;<span
                          class="origin_price line"></span><span class="origin_price line">{{marketPrice}}</span></p>
                        <p class="package_introduce">
                          {{packageDescription}}
                        </p>
                      </div>
                    </el-col>
                  </el-row>
                </div>
              </div>
              <el-pagination
                background
                layout="prev, pager, next"
                :total="100">
              </el-pagination>
            </el-tab-pane>
            <el-tab-pane label="" name="second">
              <div>
                <div class="store_info">
                  <el-row>

                  </el-row>
                </div>
                <div class="text_align_left padding_left10">
                  <p class="stores_name"></p>
                  <p class="stores_introduce">
                   {{description}}
                  </p>
                  <!--<p class="stores_introduce">-->
                    <!--9106000;;;-->
                  <!--</p>-->
                </div>
                <div class="text_align_left padding_left10">
                  <p class="stores_name"></p>
                  <p class="stores_introduce">{{institutionLocate}}</p>
                  <div>
                    <img src="../../assets/medicalproduct/ditu_img@2x.png" height="402"/></div>
                </div>
              </div>
            </el-tab-pane>
          </el-tabs>
        </el-col>
        <el-col :span="5">
          <div class="float_left regular_font">
            
          </div>
          <el-row>
            <el-col :span="20" v-for="(o, index) in 2" :key="o">
              <el-card :body-style="{ padding: '0px' }">
                <img src="../../assets/checkUp/taocan_img1@2x.png" class="image">
                <div class="card_content card_align">
                  <p class="card_title">()</p>
                  <span class="card_subtitle"></span>
                  <p class="margin_top0_bottom0"><span class="discount_price"></span><span
                    class="discount_price">558</span><span class="origin_price"></span>&emsp;&emsp;&emsp;<span
                    class="origin_price line"></span><span class="origin_price line">892</span></p>
                </div>
              </el-card>
            </el-col>
          </el-row>
        </el-col>
      </el-row>

    </div>
  </div>
  
</template>
<script>
  export default {
    data() {
      return {
        needingAttention:'',
        suitNumber:0,
        suitPrice:0,
        suitName:'',
        suitSex: 0,//1 2 0
        salePrice: '',
        marketPrice: '0',
        medicalCenter:'',
        packageDescription: '....',//
        institutionId: '',//id
        active: false,
        activeName: 'first',

        storesName: '',
        storesAddress: '',
        storePhone: '4001-1211-00',
        businessHours: '8:00-11:3013:00-17:00',
        institutionLocate: '910',
        description:'',
      }
    },
    created() {
      this.getInstitutionInfo();
      //
      // this.getMedicalSuitList();
    },
    methods: {
      getInstitutionInfo() {
        let url = '/api/user/institutions/' + this.$route.query.institutionId;
        this.$get(url, {}).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            console.log("detail==" + JSON.stringify(res.data));
            let institutionDetail = res.data.data.institutionDetail;
            this.businessHours = institutionDetail.businessHours;
            this.storesName = institutionDetail.name;
            this.storePhone = institutionDetail.telephone;
            this.storesAddress = institutionDetail.province + institutionDetail.city;
            this.needingAttention = institutionDetail.note;
            this.suitNumber = institutionDetail.productAccount;
            this.suitPrice = institutionDetail.mineSale;
            this.description = institutionDetail.description;
            this.institutionLocate = institutionDetail.address;
            //
            // this.getInstitutionRelate();
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      //-
      getMedicalSuitList() {
        let url = '/api/user/institution/packages/' + this.institutionId;
        this.$get(url, {}).then(res => {
          debugger
          if (JSON.stringify(res.data.code) == 200) {
            console.log("==" + JSON.stringify(res.data));
            // let productDetail = res.data.data.productDetail
            // this.medicalCenter = productDetail.centerName;
            // this.productName = productDetail.productName;
            // this.salePrice = productDetail.salePrice;
            // this.marketPrice = productDetail.marketPrice;
            // this.packageDescription = productDetail.packageDescription;
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      getMoreInstitution() {
        let url = '/api/user/product/institutions/' + this.$route.query.productId;
        this.$get(url, {}).then(res => {
          debugger
          if (JSON.stringify(res.data.code) == 200) {
            console.log("==" + JSON.stringify(res.data));
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      cartHandleChange(value) {
        console.log(value);
      },
      selectSuit() {
        this.active = true;
      },
      handleClick(tab, event) {
        console.log(tab, event);
      },
      // table header
      tableHeaderColor({row, column, rowIndex, columnIndex}) {
        if (rowIndex === 0) {
          return 'background-color: #F6F6F6;color: #404040;font-weight: 500;'
        }
      }
    }
  }
</script>
<style>
  @import "../../styles/personal/personal-center.scss";

  .margin_left_right {
    margin-left: 10%;
    margin-right: 10%;
  }

  .hr_color {
    height: 2px;
    background-color: #e6e6e6;
    border: none;
  }

  .border {
    margin-top: 10px;
    border: 1px solid #e6e6e6;
    background-color: #EEEEEE;
    width: 280px;
    height: 220px;
  }

  .title_font {
    color: #333333;
    font-size: 18px;
    margin-top: 8px;
    margin-bottom: 8px;
  }

  .special_font {
    color: #009BDD;
    font-size: 14px;
  }

  .sub_font {
    color: #a2a2a2;
    font-size: 14px;
    margin-top: 6px;
    margin-bottom: 6px;
  }

  .second_font {
    color: #666666;
    font-size: 16px;
    margin-top: 6px;
    margin-bottom: 6px;
  }

  .regular_font {
    color: #666666;
    font-size: 14px;
    margin-top: 6px;
    margin-bottom: 6px;
  }

  .select_suit {
    background-color: #33B2E8;
    color: #FFFFFF;
    /*border: 1px solid #DF2321;*/
  }

  .clickFont {
    background-color: #DF2321;
    color: #ffffff;
  }

  .tab_bg_color {
    border: 1px solid rgba(238, 238, 238, 1);
    background-color: #EEEEEE;
  }

  /**/
  .institution_img {
    margin-top: 10px;
    margin-bottom: 10px;
    border: 1px solid #e6e6e6;
    background-color: #EEEEEE;
    width: 200px;
    height: 150px;
  }

  .store_info {
    border: 1px solid rgba(238, 238, 238, 1);
  }

  .icon_img {
    vertical-align: middle;
  }

  .stores_name {
    font-size: 16px;
    color: #404040;
  }

  .stores_introduce {
    font-size: 12px;
    color: #666666;
  }

  .select_detail {
    float: right;
    padding-right: 10px;
  }
  .package_introduce{
    font-size: 14px;
    color: #a2a2a2;
  }
</style>





