<template>
  <div>
    <el-row class="white_bg_color">
      <el-col :span="12">
        <el-header class="header_bg">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item>{{asideNavTitle[0]}}</el-breadcrumb-item>
            <el-breadcrumb-item>{{asideNavTitle[1]}}</el-breadcrumb-item>
          </el-breadcrumb>

        </el-header>
      </el-col>
      <el-col :span="12" class="margin_top15">
        <el-button class="add_medical_plan" :class="{displayClass:whether}" @click="addMedicalPlan()"></el-button>
      </el-col>
    </el-row>
    <div :class="{displayClass:whether}">
      <div class="margin_top15 white_bg_color">
        <el-row>
          <el-col :span="12">
            <p class="medical_title text_align_left padding_left10"></p>
            <p class="plan_name text_left">{{planName}}</p>
            <el-row>
              <el-col :span="12">
                <p class="plan_number_font margin_top0">{{medicalCount}}</p>
                <p class="medical_title margin_top0"></p>
              </el-col>
              <el-col :span="12">
                <p class="plan_number_font margin_top0">{{planMoney}}</p>
                <p class="medical_title margin_top0"></p>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="12">
                <p class="add_new_medical_plan margin_top0" @click="addMedicalPlan()"><i
                  class="el-icon-circle-plus"></i>
                </p>
              </el-col>
              <el-col :span="12">
                <p class="state_title_font margin_top0"><span class="state_font">{{active===1?'':''}}</span>
                </p>
                <p class="state_title_font margin_top0">{{addDate}}</p>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="12" class="left_box">
            <el-row>
              <el-col :span="12">
                <p class="medical_title text_align_left padding_left10"></p>
              </el-col>
              <el-col :span="12">
                <p class="add_new_medical_plan"><i class="el-icon-setting"></i></p>
              </el-col>
            </el-row>

            <p class="plan_name text_left">{{settingState}}</p>
            <p class="setting_hint text_left"></p>
            <hr class="hr_border">
            <el-row>
              <el-col :span="12">
                <p class="medical_title text_align_left padding_left10"></p>
              </el-col>
              <el-col :span="12">
                <p class="add_new_medical_plan"><i class="el-icon-setting"></i></p>
              </el-col>
            </el-row>
            <p class="plan_name text_left">{{medicalCycle}}</p>
            <p class="setting_hint text_left"></p>
          </el-col>
        </el-row>
      </div>
      <div class="white_bg_color margin_top15">
        <el-row>
          <el-col :span="12">
            <p class="medical_title text_align_left padding_left10 padding_top10"></p>
          </el-col>
          <el-col :span="12">
            <p class="add_new_medical_plan text_align_right padding_right10 padding_top10"
               @click="moreMedicalHistory()"></p>
          </el-col>
        </el-row>
        <div class="padding_left10 padding_right10">
          <el-table
            :data="tableData"
            height="250"
            style="width: 100%" :header-cell-style="tableHeaderColor">
            <el-table-column
              prop="name"
              label="">
            </el-table-column>
            <el-table-column
              prop="employeeNumber"
              label="">
            </el-table-column>
            <el-table-column
              prop="payout"
              label="">
            </el-table-column>
            <el-table-column
              prop="averageLimit"
              label="">
            </el-table-column>
            <el-table-column
              prop="createDate"
              label="">
            </el-table-column>
            <el-table-column
              fixed="right"
              label=""
              width="100">
              <template slot-scope="scope">
                <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>

      </div>
    </div>
    <!-- -->
    <div :class="{displayClass:whetherHistory}">
      <medical-history></medical-history>
    </div>
    <!---->
    <!--<div>-->
    <!--<el-dialog title="" :visible.sync="medicalPlanDialog" width="35%">-->
    <!--<el-form :model="createPlan">-->
    <!--<el-form-item label="" :label-width="formLabelWidth">-->
    <!--<el-input v-model="createPlan.date" autocomplete="off" placeholder=""></el-input>-->
    <!--</el-form-item>-->
    <!--<el-form-item label="" :label-width="formLabelWidth">-->
    <!--<el-input v-model="createPlan.name" autocomplete="off" placeholder=""></el-input>-->
    <!--</el-form-item>-->
    <!--</el-form>-->
    <!--<div slot="footer" class="dialog-footer">-->
    <!--<el-button @click="medicalPlanDialog = false"> </el-button>-->
    <!--<el-button type="primary" @click="confirmCreateMedicalPlan()"> </el-button>-->
    <!--</div>-->
    <!--</el-dialog>-->
    <!--</div>-->
    <!---->
    <div :class="{displayClass:whetherAddPlan}" class="white_bg_color">
      <add-medical-plan></add-medical-plan>
      <!--<div>-->
      <!--<p class="prompt_font"><el-button class="create_medical_plan" @click="createMedicalPlan()"></el-button></p>-->
      <!--</div>-->
    </div>
    <!---->
    <div :class="{displayClass:whetherCreatePlan}" v-if="createPlanFlag==true">
      <create-medical-plan></create-medical-plan>
    </div>
  </div>
</template>

<script>
  import MedicalHistory from '../../../src/components/firm/MedicalHistory'
  import AddMedicalPlan from '../../../src/components/firm/AddMedicalPlan'
  import CreateMedicalPlan from '../../../src/components/firm/CreateMedicalPlan'
  import {mapGetters} from 'vuex'
  import swal from 'sweetalert2'; //alert
  import Verify from '../../utils/utils';

  export default {
    name: "MedicalPlan",
    components: {
      AddMedicalPlan,
      MedicalHistory,
      CreateMedicalPlan
    },
    data() {
      return {
        medicalPlan: '>',
        planName: '1',
        medicalCount: 100,//
        planMoney: '10000.00',//
        active: 1,//
        addDate: '2019-01-14',//
        settingState: '',//
        medicalCycle: '3 9',
        whether: false,// 
        whetherHistory: true,//
        whetherAddPlan: true,//
        whetherCreatePlan: true,//
        tableData: [],
        formLabelWidth: '120px'
      }
    },
    computed: {
      ...mapGetters(['asideNavTitle','createPlanFlag'])
    },
    created() {
      this.getStaffPlanList();
      console.log("createPlanFlag000==" + this.createPlanFlag);
      // if (this.createPlanFlag == false) {
      //   this.confirmCreateMedicalPlan();
      // }
    },
    methods: {
      //
      getStaffPlanList() {
        let enterpriseId = localStorage.getItem("enterpriseId");
        let getStaffUrl = '/api/enterprise/physicalExamiantionPlan/' + enterpriseId + '/home';
        this.$get(getStaffUrl, {
          enterpriseId: enterpriseId,
        }).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            // this.tableData = res.data.data;

            console.log("staff==" + JSON.stringify(res.data.data))
            //
            var physicalExamiantionPlan = JSON.stringify(res.data.data.physicalExamiantionPlan);
            if (Verify.validateIsNull(physicalExamiantionPlan)) {
              //
              if (Verify.validateIsNull(physicalExamiantionPlan.name)) {
                this.planName = physicalExamiantionPlan.name;
              }
              //
              if (Verify.validateIsNull(physicalExamiantionPlan.employeeNumber)) {
                this.medicalCount = physicalExamiantionPlan.employeeNumber;
              }
              //
              if (Verify.validateIsNull(physicalExamiantionPlan.sumMoney)) {
                this.planMoney = physicalExamiantionPlan.sumMoney;
              }
              //
              if (Verify.validateIsNull(physicalExamiantionPlan.status)) {
                this.active = physicalExamiantionPlan.status;
              }

              //
              if (Verify.validateIsNull(physicalExamiantionPlan.createdA)) {
                this.addDate = physicalExamiantionPlan.createdA;
              }
            }
            //
            var setMeal = JSON.stringify(res.data.data.setMeal);
            if (Verify.validateIsNull(setMeal)) {
              if (Verify.validateIsNull(setMeal.settingStatus)) {
                this.settingState = setMeal.settingStatus;
              }
            }
            //
            var cycleRemind = JSON.stringify(res.data.data.cycleRemind);
            if (Verify.validateIsNull(cycleRemind)) {
              if (Verify.validateIsNull(cycleRemind.cycle))
                this.medicalCycle = cycleRemind[0].cycle + cycleRemind[0].cyclePeriod;
            }
            var physicalPlanList = JSON.stringify(res.data.data.physicalExamiantionPlanList);
            if (Verify.validateIsNull(physicalPlanList)) {
              if (Verify.validateIsNull(physicalPlanList.items)) {
                this.tableData = physicalPlanList.items;
                for (const i in this.tableData) {
                  let createDate = Verify.convertDate(this.tableData[i].createdAt);
                  this.tableData[i]['createDate'] = createDate;
                }
              }

            }
            // swal("", "", "success");
          } else {
            swal(JSON.stringify(res.data.message), "", "error");
          }
        }).catch(function (reason) {
          console.log(JSON.stringify(reason));
        });
      },

      //
      addMedicalPlan() {
        this.whether = true;
        this.whetherHistory = true;
        this.whetherAddPlan = false;
        this.medicalPlan = '>>';
      },
      handleClick(row) {
        console.log(row);
      },
      //
      moreMedicalHistory() {
        this.whether = true;
        this.whetherAddPlan = true;
        this.whetherHistory = false;
        this.medicalPlan = '>>';
      },
      //
      createMedicalPlan() {
        this.medicalPlanDialog = true;

      },
      //
      confirmCreateMedicalPlan() {
        // /api/enterprise/physicalExamiantionPlan/:enterpriseId
        this.whether = true;
        this.whetherAddPlan = true;
        this.whetherHistory = true;
        this.medicalPlanDialog = false;
        this.whetherCreatePlan = false;
        this.medicalPlan = '>';
        // this.$store.dispatch('setStuffPlanInfo','1')
      },
      // table header
      tableHeaderColor({row, column, rowIndex, columnIndex}) {
        if (rowIndex === 0) {
          return 'background-color: #F6F6F6;color: #404040;font-weight: 500;'
        }
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/personal/personal-center.scss";

  .dashed_hr {
    height: 5px;
    border: none;
    border-top: 3px dashed #6f7180;

  }

  .header_bg {
    background-color: white;
    text-align: right;
    font-size: 18px;
    color: #333333;
  }

  .el-breadcrumb {
    font-size: 18px;
    line-height: 3.5;
    color: #333333;
  }

  .text_left {
    text-align: left;
    padding-left: 10px;
    margin-top: 0px;
    margin-bottom: 5px;
  }

  .left_box {
    margin-top: 5px;
    border-left: 1px dashed #F6F6F6;
  }

  .hr_border {
    margin-left: 10px;
    margin-right: 10px;
    border: 1px dashed #F6F6F6;
  }

  .medical_title {
    font-size: 14px;
    color: #404040;
  }

  .prompt_font {
    font-size: 14px;
    color: #666666;
  }

  .plan_name {
    font-size: 18px;
    color: #666666;
  }

  .plan_number_font {
    font-size: 30px;
    color: #009BDD;
    margin-top: 0px;
    margin-bottom: 0px;
  }

  .add_medical_plan {
    color: #FFFFFF;
    font-size: 17px;
    background-color: #33B2E8;
    width: 202px;
  }

  .add_new_medical_plan {
    color: #009BDD;
    font-size: 16px;
  }

  .state_title_font {
    font-size: 12px;
    color: #666666;
  }

  .state_font {
    font-size: 12px;
    color: green;
  }

  .setting_hint {
    color: #AAAAAA;
    font-size: 12px;
  }

  .displayClass {
    display: none;
  }

  .create_medical_plan {
    color: #FFFFFF;
    background-color: #33B2E8;
    width: 180px;
    font-size: 17px;
  }
</style>



