<template>  
  <div class="all_category">
    <div class="category">
      <div class="div_bottom margin_top5">
        <el-row>
          <el-col :span="2" class="statistics_font">
            <div class="btn_bg_color"></div>
          </el-col>
          <el-col :span="18" class="regular_font">
            <div>
              <ul class="ul_list_style">
                <li v-for="item in suitType" class="padding_left10 padding_right10">
                  <a v-text="item.name" v-on:click="selectType(item.id,item.name)"
                     v-bind:class="{active : selectTypeName === item.name }"></a>
                </li>
              </ul>
            </div>
          </el-col>
          <el-col :span="3">
            <el-button size="small"></el-button>
          </el-col>
        </el-row>
      </div>
      <div class="div_bottom">
        <el-row>
          <el-col :span="2" class="statistics_font">
            <div class="btn_bg_color"></div>
          </el-col>
          <el-col :span="18" class="regular_font">
            <div>
              <ul class="ul_list_style">
                <li v-for="project in projectType" class="padding_left10 padding_right10">
                  <a v-text="project.proName" v-on:click="selectType(project.id,project.proName)"
                     v-bind:class="{active : selectTypeName === project.proName }"></a>
                </li>
              </ul>
            </div>
          </el-col>
          <el-col :span="3">
            <el-button size="small"></el-button>
          </el-col>
        </el-row>
      </div>
      <div class="div_bottom">
        <el-row>
          <el-col :span="2" class="statistics_font">
            <div class="btn_bg_color"></div>
          </el-col>
          <el-col :span="18" class="regular_font">
            <div>
              <ul class="ul_list_style">
                <li v-for="item in ageCategory" class="padding_left10 padding_right10">
                  <a v-text="item.period" v-on:click="selectType(item.id,item.period)"
                     v-bind:class="{active : selectTypeName === item.period }"></a>
                </li>
              </ul>
            </div>
          </el-col>
        </el-row>
      </div>

      <div class="div_bottom">
        <el-row>
          <el-col :span="2" class="statistics_font">
            <div class="btn_bg_color"></div>
          </el-col>
          <el-col :span="18" class="regular_font">
            <div>
              <ul class="ul_list_style">
                <li class="padding_left10 padding_right10">
                  
                </li>
                <li class="padding_left10 padding_right10">
                  
                </li>
              </ul>
            </div>
          </el-col>
        </el-row>
      </div>

    </div>
    <div class="suit_list">
      <el-row class="regular_font">
        <el-col :span="2">
          <div class="suit_btn"></div>
        </el-col>
        <el-col :span="2">
          <div></div>
        </el-col>
        <el-col :span="2">
          <div></div>
        </el-col>
        <el-col :span="4">
          <div></div>
        </el-col>
        <el-col :span="14" class="text_align_right">
          <div class="statistics_font"><span class="eligible_number">{{eligibleNumber}}</span></div>
        </el-col>
      </el-row>
    </div>
    <div>
      <el-row :gutter="40">
        <el-col :span="18">
          <el-row :gutter="40">
            <el-col :span="8" v-for="(p, index) in productData" :key="index" :class="{'marginTop10':index>2}">
              <el-card :body-style="{ padding: '0px' }" @click="productDetail(p.id)">
                <img src="../../assets/checkUp/taocan_img1@2x.png" class="image"  @click="productDetail(p.id)">
                <div class="card_content">
                  <p class="card_title">{{p.name}}</p>
                  <span class="card_subtitle">{{p.simpleDescription}}</span>
                  <p class="margin_top0_bottom0"><span class="discount_price"></span><span
                    class="discount_price">{{p.salePrice}}</span><span class="origin_price"></span>&emsp;&emsp;&emsp;<span
                    class="origin_price line"></span><span class="origin_price line">{{p.marketPrice}}</span></p>
                </div>
              </el-card>
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="6">
          <div class="float_left regular_font">
            
          </div>
          <el-row>
            <el-col :span="20" v-for="(o, index) in 4" :key="o">
              <el-card :body-style="{ padding: '0px' }">
                <img src="../../assets/checkUp/taocan_img1@2x.png" class="image">
                <div class="card_content card_align">
                  <p class="card_title">()</p>
                  <span class="card_subtitle"></span>
                  <p class="margin_top0_bottom0"><span class="discount_price"></span><span
                    class="discount_price">558</span><span class="origin_price"></span>&emsp;&emsp;&emsp;<span
                    class="origin_price line"></span><span class="origin_price line">892</span></p>
                </div>
              </el-card>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        period: '',
        selectTypeName: '',
        suitType: [{'id': '1', 'name': ''}, {'id': '2', 'name': ''}, {'id': '3', 'name': ''}, {
          'id': '4',
          'name': ''
        }, {'id': '5', 'name': ''}],
        projectType: [{'id': '1', 'proName': ''}, {'id': '2', 'proName': ''}, {
          'id': '3',
          'proName': ''
        }, {'id': '4', 'proName': ''}, {'id': '5', 'proName': ''}],
        ageCategory: [{'id': '10', 'period': ''}, {'id': '11', 'period': '18'}, {
          'id': '12',
          'period': '1828'
        }, {'id': '13', 'period': '3545'}, {'id': '14', 'period': '4545'}, {
          'id': '11',
          'period': '5565'
        }, {'id': '15', 'period': '65'}],
        eligibleNumber: 256,
        productData:[],
      }
    },
    created(){
      this.getProductList();
    },
    methods: {
      getProductList() {
        // let userId = localStorage.getItem("userId");
        let product = '/api/user/products';
        this.$get(product, {}).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            this.productData = res.data.data.items;
            console.log("test" + JSON.stringify(res.data.data));
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      productDetail(id){
        this.$router.push({ path: '/MedicalProductDetail',query:{productId:id}})
      },
      selectType(params) {
        console.log("params===" + params);
      },
    }
  }
</script>
<style>
  @import "../../styles/personal/personal-center.scss";

  .all_category {
    padding-left: 10%;
    padding-right: 10%;
    /*margin-top: 15px;*/
    line-height: 38px;
  }

  .category {
    border: 1px solid #EEEEEE;
    background-color: #ffffff;
  }

  .regular_font {
    color: #333333;
    font-size: 14px;
  }

  .btn_bg_color {
    background-color: #f8f8f8;
    height: 38px;
  }

  div.category ul {
    list-style: none; /* ul */
    margin: 0px; /* 0 */
    padding: 0px; /* 0 */
    width: auto; /*  */
  }

  /* classmenudivulli */
  div.category ul li {
    float: left; /*  */
  }

  .div_bottom {
    border-bottom: 1px solid #EEEEEE;
  }

  .margin_top5 {
    margin-top: 5px;
  }

  /**/
  .suit_list {
    background-color: white;
    margin-top: 10px;
    margin-bottom: 10px;
    height: 38px;
  }

  .suit_btn {
    height: 38px;
  }

  .eligible_number {
    color: #33B2E8;
    font-size: 14px;
  }

  .statistics_font {
    color: #666666;
    font-size: 14px;
  }
  .marginTop10 {
    margin-top: 20px;
  }
  .card_align{
    text-align: left;
    padding-left: 20px;
    line-height: 30px;
  }

</style>



